import React from "react"
import styled from "styled-components"
const Wrap = styled.div`
  margin-bottom: 0.5rem;
  padding-bottom: 0.5rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 1px solid #e9e9e9;
  & .alert {
    padding: 0.5rem;
    margin-bottom: 0.5rem;
  }
  & .a-label {
    width: 200px;
    max-width: 100%;
  }
  & .a-control {
    flex: 1;
    min-width: 200px;
  }
`
export default function MFCWrapControl(props) {
  const { cfc } = props
  return (
    <Wrap className="mfcw-inline">
      <div className="a-label">{cfc.label}</div>
      <div className="a-control">{props.children}</div>
    </Wrap>
  )
}
